.Login-div {
  display: grid;
}

.login-div {
  display: flex;
  background-color: lightblue;
  justify-content: space-evenly;
  padding: 10px;
  border-bottom: 1px solid black;
}

.login-label {
  font-weight: 500;
}

.login-button {
  cursor: pointer;
}

.register-div {
  display: flex;
  justify-content: end;
  padding-top: 100px;
  padding-right: 40px;
}

.register-div-parent {
  display: grid;
  background-color: lightcyan;
  border: 1px solid black;
  padding: 10px 15px;
  gap: 10px;
}

.register-div-child:nth-child(1) {
  border: 1px solid black;
  font-weight: 700;
}

.register-div-child:nth-child(2) {
  display: flex;
  gap: 10px;
}