.Sidebar-div {
  display: flex;
}

.sidebar-div {
  background-color: lightskyblue;
  height: 555px;
  border-right: 1px solid black;
}

.sidebar-search {
  position: relative;
  display: flex;
  gap: 5px;
  border-bottom: 1px solid black;
}

.chat-button-div:nth-child(1) {
  position: relative;
  margin-top: 10px;
}

.chat-button {
  width: 100%;
  cursor: pointer;
}

.chat-button-div-child {
  display: flex;
  align-items: center;
  gap: 20px;
}