.Chat-div {
  display: flex;
  flex-direction: column;
  width: 81.9%;
  gap: 10px;
}

.chat-div-child {
  display: grid;
  justify-content: start;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  background-color: lightgreen;
}

.message-input-div {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 10px;
  left: 230px;
  width: 1100px;
  gap: 10px;
}

.message-input {
  height: 100%;
  width: 90%;
  border: 1px solid black;
  padding: 5px 0px;
}

.send-button {
  cursor: pointer;
  font-weight: 500;
  border: 1px solid black;
  border-radius: 20px;
}

.user-chats {
  display: flex;
  gap: 20px;
}

.user-name {
  display: flex;
  font-weight: 700;
}

.chat {
  text-align: start;
  font-weight: 500;
}

.chat-div-child-reverse {
  display: grid;
  justify-content: end;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
}

.chat-reverse {
  text-align: end;
  font-weight: 500;
}

.user-chats-reverse {
  display: flex;
  gap: 20px;
}

.user-name-reverse {
  display: flex;
  font-weight: 700;
}