.navbar-div {
  display: flex;
  justify-content: space-around;
  background-color: lightpink;
  border-bottom: 1px solid black;
  padding: 10px;
}

.profile-picture {
  border: 1px solid black;
  border-radius: 20px;

}

.navbar-div-child:nth-child(1) {
  font-weight: 700;
}

.navbar-div-child:nth-child(2) {
  display: flex;
  gap: 10px;
}

.navbar-div-child:nth-child(3) {
  display: flex;
  align-items: center;
  gap: 10px;
}

.search-friends-input {
  width: 400px;
}

.search-friends-button {
  cursor: pointer;
}