.AddFriends {
  position: absolute;
  top: 42px;
  left: 420px;
  width: 403px;
  background-color: lightgreen;
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}

.add-friends:nth-child(1) {
  display: flex;
  justify-content: end;
  border-bottom: 1px solid black;
  padding: 10px 15px;

}

.add-friends {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0px 10px;
}

.close-button {
  border-radius: 50%;
  cursor: pointer;
  border: 1px solid black;
  background-color: white;
}

.add-friend-button {
  cursor: pointer;
  border-radius: 20px;
  border: 1px solid black;
  padding: 5px 8px;
}